.menu {
  display: flex;
  text-align: center;
  min-height: 20px;
  justify-content: center;
  margin: 10px auto;
}

.menu a {
  color: white;
  text-decoration: none;
  font-size: 30px;
  font-weight: bold;
  margin: 0 20px;
  transition-duration: 0.4s;
}

.menu a:hover {
  color: #61dafb;
}
